import Vue from 'vue'
import Router from 'vue-router'

import Index from '@/components/index.vue'
import Daily from '@/components/daily.vue'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',   
			name: 'index',
			component: Index
		},
		{
			path: '/daily',
			name: 'daily',
			component:Daily
		}
	]
})