        jh:1,jh_sum:0,jh_timeout:0,
        zq:1,zq_sum:0,zq_timeout:0,
        qt:1,qt_sum:0,qt_timeout:0
      }
      for(let i in data){
        if(data[i].TOP800 != '0' || data[i].level != '一级'&& data[i].level != '二级'){
          continue;
        }
        result.sum++;
        let major;
        switch(data[i].major){
